.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about__me {
  width: 100%;
  height: 100%;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}

.about__me-image {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition-duration: 1s;
}

.about__me-image:hover {
  transform: rotate(0);
  /* transition: var(--transition);  */
}

.about__me-image img {
  height: 500px;
}
.about__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}

.about__card {
  background-color: var(--color-primary);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  opacity: 0.75;
  transition-duration: 0.8s;
}

.about__card:hover {
  background: transparent;
  border-color: var(--color-primary);
  color: var(--color-light);
}

.about__icon {
  color: var(--color-bg-variant);
  font-size: 2rem;
  margin-bottom: 1rem;
  transition-duration: 0.8s;
}

.about__card:hover .about__icon {
  color: var(--color-white);
}

.about__card h5 {
  font-size: 0.95rem;
  color: var(--color-bg-variant);
  transition-duration: 0.8s;
}
.about__card:hover h5 {
  color: var(--color-white);
}

.about__card:hover small {
  color: var(--color-white);
}

.about__card small {
  font-size: 0.7rem;
  color: var(--color-bg-variant);
  transition-duration: 0.8s;
}

.about__content p {
  margin: 2rem 0 2.6rem;
  color: var(--color-light);
}

.about__content a {
  padding: 10px 10px;
}

/* =============================Media Queries ============================ */
@media screen and (max-width: 1250px) {
  .about__me {
    width: 100%;
    height: 95%;
  }
}
@media screen and (max-width: 1107px) {
  .about__me {
    width: 100%;
    height: 90%;
  }
}

@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about__me {
    width: 50%;
    height: 83%;
    margin: 2rem auto 4rem;
  }

  .about__me p {
    margin: 1rem 0 1.5rem;
  }
}

@media screen and (max-width: 600px) {
  .about__me {
    width: 65%;
    height: 65%;
    margin: 0 auto 3rem;
  }

  .about__cards {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    margin-top: 100px;
  }

  .about__content {
    text-align: center;
  }

  .about__content p {
    margin: 1.5rem 0;
  }
}
